import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Select, DatePicker } from 'antd';
import { teachers } from 'dump/data';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    academicRank: '',
    degree: '',
    title: '',
    position: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormStuPartyMem extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const teacherMap = teachers.filter(teacher => teacher.code === code)[0];
    const initialValue = handleInitialValue(teacherMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    const config = {
      rules: [{ type: 'object', message: 'Please select time!', required: true }],
    };
    return (
      <Card title={teacherId ? 'Đảng viên là sinh viên' : 'Đảng viên là sinh viên'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Loại đối tượng" {...formItemLayout}>
              {getFieldDecorator('typeObject', {
                initialValue: initialValue.typeObject,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select defaultValue="ok">
                  <Option value="ok">OK</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Từ ngày" {...formItemLayout}>
              {getFieldDecorator('date-picker', config)(<DatePicker />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#party-mem/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStuPartyMem {...props} />
  </Page>
));



